  display: block;
  position: relative;
  -webkit-transition: all .4s linear;
  transition: all .4s linear;
}
ul.claim-photo-section li.claim-photo-item .hovereffect h2 {
  text-transform: uppercase;
  color: #fff;
  text-align: center;
  position: relative;
  font-size: 17px;
  background: rgba(0, 0, 0, 0.6);
  -webkit-transform: translatey(-100px);
  -ms-transform: translatey(-100px);
  transform: translatey(-100px);
  -webkit-transition: all 0.1s ease-in-out;
  transition: all 0.1s ease-in-out;
  padding: 10px;
}
ul.claim-photo-section li.claim-photo-item .hovereffect a.info {
  text-decoration: none;
  display: inline-block;
  text-transform: uppercase;
  color: #fff;
  border: 1px solid #fff;
  background-color: transparent;
  opacity: 0;
  filter: alpha(opacity=0);
  -webkit-transition: all 0.1s ease-in-out;
  transition: all 0.1s ease-in-out;
  margin: 50px 5px 0px 5px;
  padding: 7px;
  height: auto;
}
ul.claim-photo-section li.claim-photo-item .hovereffect a.info i.fa {
  width: 20px;
  height: 20px;
  font-size: 16px;
  padding: 0px;
}
ul.claim-photo-section li.claim-photo-item .hovereffect a.info:hover {
  box-shadow: 0 0 5px #fff;
}
ul.claim-photo-section li.claim-photo-item .hovereffect:hover img {
  -ms-transform: scale(1.2);
  -webkit-transform: scale(1.2);
  transform: scale(1.2);
}
ul.claim-photo-section li.claim-photo-item .hovereffect:hover .overlay-pane {
  opacity: 1;
  filter: alpha(opacity=100);
}
ul.claim-photo-section li.claim-photo-item .hovereffect:hover h2,
ul.claim-photo-section li.claim-photo-item .hovereffect:hover a.info {
  opacity: 1;
  filter: alpha(opacity=100);
  -ms-transform: translatey(0);
  -webkit-transform: translatey(0);
  transform: translatey(0);
}
ul.claim-photo-section li.claim-photo-item .hovereffect:hover a.info {
  -webkit-transition-delay: .1s;
  transition-delay: .1s;
}
